@import '@assets/styles/_variables';

.impty{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: calc(var(--index) * 22);
    margin: 0 auto;
    padding: calc(var(--index) * 2);
}

.imptyTitle{
    font-size: calc(var(--index) * 1.3);
    padding-bottom: calc(var(--index) * 0.8);
    text-align: center;
}

.imptySubTitle{
    text-align: center;
    font-size: calc(var(--index) * 0.9);
    padding-bottom: calc(var(--index) * 1);
}

.imptyInner{
    position: relative;
    border-radius: 50%;
    width: 300px;
    height: 300px;
    display: flex;
    overflow: hidden;
    justify-content: center;
}

.imptyInnerColorCart{
    background-color: #409122c2;
}

.imptyInnerColorFavorite{
    background-color: #ffff0069;
}

.imptyItem{
    position: absolute;
    top: 45px;
    width: 155px;
}

